import { Typography } from 'antd';
import config from '../config';
import './index.css';

function App() {
  return (
    <div className="header-app">
      <div className="header-backlinks-fixed">
        <div className="header-backlinks">
          {config.backlink.map(_ => {
            return (
              <div key={_.url}>
                <Typography.Link href={_.url} target="_blank" rel="noreferrer">
                  {_.name}
                </Typography.Link>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}

export default App;
